import { Component, render, createElement } from './toy-react'
 
class App extends Component {
    constructor(){
        super();
        this.state = {
            count: 1,
        }
    }

    render(){
        const  { count } = this.state;
        return <div>
            <h1>my component title</h1>
            <p>
                <span>counts: {count}</span>
                <br />
                <button onClick={() => this.setState({count: count++})}>+</button>
                <button onClick={() => this.setState({count: count--})}>-</button>
            </p>
            {this.children}
        </div>
    }
}

render(<div>sss</div>, document.body)

